<template>
  <v-main class="black">
    <div class="pa-8"></div>
    <v-card height="700" width="600" class="mx-auto card">
      <div class="pa-4"></div>
      <v-card class="pa-7" flat color="transparent">
        <v-container>
          <v-row justify="center" align="start" class="text-center">
            <v-img
              :src="require('@/assets/images/cmet_logo.webp')"
              max-width="60%"
              :min-width="'calc(70% - ' + $vuetify.application.top + 'px)'"
            />
          </v-row>
        </v-container>
      </v-card>

      <div class="pa-6"></div>

      <v-form ref="form" lazy-validation>
        <v-row justify="center">
          <v-col cols="10" md="8">
            <v-text-field
              dark
              outlined
              rounded
              v-model="user.account"
              required
              clearable
              color="green accent-2"
            >
              <template v-slot:label>
                <p class="mx-4 white--text">Account Number (Email)</p>
              </template>
            </v-text-field>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col cols="10" md="8">
            <v-text-field
              dark
              type="password"
              outlined
              rounded
              v-model="user.password"
              required
              clearable
              color="green accent-2"
            >
              <template v-slot:label>
                <p class="mx-4 white--text">Password</p>
              </template>
            </v-text-field>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col cols="12">
            <p class="red--text text-center">{{ errorMsg }}</p>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col class="text-center mb-6" cols="12">
            <v-btn
              type="submit"
              class="input"
              depressed
              rounded
              elevation="0"
              color="green accent-2"
              dark
              :width="$vuetify.application.top + 200 + 'px'"
              :max-height="$vuetify.application.top + 'px'"
              :min-height="$vuetify.application.top - 50 + 'px'"
              @click.prevent="submit"
            >
              <span style="font-size: 24px">Log in</span>
            </v-btn>
          </v-col>
          <v-col class="text-center" cols="12">
            <span class="white--text pt-6 mt-6">Don't have an account?</span>
            <v-btn
              class="login-btn pb-1"
              text
              depressed
              dark
              :to="{ name: 'Register' }"
            >
              <span style="font-size: 14px">Register</span>
            </v-btn>
          </v-col>
          <v-col class="text-center" cols="12">
            <v-btn
              class="login-btn"
              text
              depressed
              dark
              :to="{ name: 'ForgetPassword' }"
            >
              <span style="font-size: 14px">Forget password?</span>
            </v-btn>
          </v-col>
        </v-row>
      </v-form>
    </v-card>
    <div class="pa-16"></div>
  </v-main>
</template>

<script>
import { login } from "../api";
import md5 from "js-md5";

export default {
  data: () => ({
    date: null,
    menu: false,
    user: {
      account: "",
      password: "",
    },
    errorMsg: "",
  }),
  methods: {
    submit() {
      if (this.user.account == "") {
        this.errorMsg = "Please enter the account";
      } else if (this.user.password == "") {
        this.errorMsg = "Please enter the password";
      } else if (this.user.account && this.user.password !== "") {
        const form_data = new FormData();
        form_data.append("Email", this.user.account);
        form_data.append("PassWod", md5(this.user.password));

        login(form_data)
          .then((result) => {
            console.log(result);

            if (result.data.success) {
              this.$store.commit("$_setTokenStorage", result.data.auth_token);
              this.$store.commit("$_setAccountStorage", result.data.user_Email);
              // localStorage.setItem("auth_token", result.data.auth_token);
              localStorage.setItem("bd_token", result.data.bd_token);
              this.$router.push({ name: "Home" });
            } else {
              if (result.data.error_msg == "帳號格式錯誤請輸入手機號碼") {
                this.errorMsg = "The account is wrong, please try again";
              } else {
                this.errorMsg = "The password is wrong, please try again";
              }
            }
          })
          .catch((err) => {
            console.error(err);
          });
      }
    },
  },
};
</script>

<style lang="css">
.input {
  background: linear-gradient(to right, #00a199 0%, #00e6b4 100%);
  background-color: transparent !important;
}

.login-btn span {
  text-transform: capitalize;
  font-weight: normal;
}

.card {
  background: linear-gradient(to bottom, #002832 0%, #000000 100%);
}
</style>